  --text: #fafafa;
  --text1: rgba(242, 242, 242, 0.15); 
  --text2: rgba(242, 242, 242, 0.05);
  --background: #09090b;
  --backgroundTran: rgba(9, 9, 11, 0.1);
  --primary: #1c1917;
  --secondary: ;
  --accent: #d23b4b;
}

@font-face {
  font-family: 'g-thin';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
@font-face {
  font-family: 'g-light';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}
@font-face {
  font-family: 'g-regular';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.background-blur {
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.backdrop-shadow {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.0),
                    0 22px 70px 4px rgba(0,0,0,0.55),
                    0 0 0 1px rgba(0, 0, 0, 0.0);
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  color: var(--text);
  background-color: var(--background);
}

#sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 196px;
  height: 100vh;

  background-color: var(--backgroundTran);
  border-right: 2px solid var(--text2);

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 36px;
}

#sidebar .sidebarAction {
  margin-top: 18px;
  width: calc(100% - 36px);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#sidebar .sidebarAction div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

#profileImg {
  width: 30px;
  height: 30px;
  border: 1px solid rgba(242, 242, 242, 0.36);
  border-radius: 30px;
}
#profileName {
  font-family: 'g-regular';
  font-size: 16px;
}

#sidebarCloseBtn {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
#sidebarCloseBtn img {
  width: 28px;
  height: 28px;
  margin: 0;
  padding: 0;
}

#sidebar .taskbarAction {
  width: calc(100% - 36px);
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start; 
  gap: 12px;
}

#sidebar .taskbarAction button {
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
  border-radius: 4px;
  padding: 8px 16px;
  width: 160px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
#sidebar .taskbarAction button:hover {
  background-color: var(--text1);
  border: 1px solid var(--text2);
}
#sidebar .taskbarAction button:active{
  background-color: var(--backgroundTran);
  border: 1px solid var(--text1);
}

.sidebarIcon svg {
  height: 18px;
  width: 18px;
}

.sidebarText {
  color: var(--text);
  font-family: 'g-regular';
  font-size: 14px;
  text-align: center;
